import React from "react";
// import { Link } from "react-router-dom";
import Sida4 from "../image/sida4.jpg";

const Contenu = () => {
  return (
    <div>
      <table>
        <tr>
          <th>
            <ul style={{ marginTop: 20 }}>
              {/* <Link style={{ color: "#e52422" }}> */}
                <li style={{ color: "#e52422" }}>What is sida?</li>
              {/* </Link> */}
              <li style={{ width: 400, listStyleType: "none" ,color:'black'}}>
                Le virus de l'imunodfisience humaine (VIH) et un tipe de virus
                qui attak le sistme imunitaire du cors. Bien que l'infeksion
                par le VIH soi une affeksion chronik matrizable, si el n'est
                pas traite, el peus affaiblir le sistme imunitaire ou volu
                vers le syndrome d'immunodficience acquise (sida).
              </li>
            </ul>
          </th>
          <th>
            <img
              style={{ height: 300, width: 600, marginTop: 60 }}
              src={Sida4}
              className="Image_List"
            />
          </th>
        </tr>
      </table>
    </div>
  );
};

export default Contenu;
